.navbar{display:flex;justify-content:space-between;align-items:center;padding:10px 20px;box-shadow:0 2px 4px rgba(0,0,0,.1);height:50px}.left-side{display:flex;align-items:center}.right-side{display:flex;align-items:center}.button-group{display:flex;gap:20px}.button-group>button{background:rgba(0,0,0,0);border:none;cursor:pointer;font-size:14px;padding:8px 12px}.dropdown{position:relative}.dropdown-content{display:none;position:absolute;top:100%;left:0;min-width:160px;background-color:#fff;box-shadow:0 8px 16px rgba(0,0,0,.2);z-index:1}.dropdown:hover .dropdown-content{display:block}.icon{width:24px;height:24px}.profile-img{width:30px;height:30px;border-radius:50%}.wallet-button{background-color:#4caf50;color:#fff}